export default {
  methods: {
    submit() {
        this.send = true;    
        
        let resRequired = this.checkRequired(this.dataList);

        if (!resRequired) {
            this.send = false;

            return;
        }

        let dataForm = {};

        let formName = this.getFormName(this.dataList['type'] && this.dataList['type'].value);

        if (!formName) {
          this.send = false;            
          this.alertMessage('Произошла ошибка. Отправка с неизвестной формы.', true);

          return;
        }

        if (this.dataList.test && this.dataList.test.value != '') {
          this.send = false;            
          this.alertMessage('Произошла ошибка. Отправка с неизвестной формы.', false);

          return;
        }

        for (let input in this.dataList) {
          dataForm[input] = this.dataList[input].value;
        }

        dataForm['form_name'] = formName;

        /* $.ajax({
            url: 'http://bezpalenkoschool.online/scripts/mail.php',
            type: 'POST',
            data: dataForm,
        })
        .done((msg) => {
          console.log(msg);

          if(msg === "Ok") {
            this.send = false;
            
            this.alertMessage('Сообщение успешно отправлено.', false);
          }
          else {
            this.send = false;
            this.alertMessage('Произошла ошибка. Попробуйте позже.', true);
          }
        })
        .fail(() => {
          this.send = false;
          this.alertMessage('Произошла ошибка. Попробуйте позже.', true);
        }) */
    },
    getFormName(brief) {
      const formNameList = {
        pages: 'Форма со страницы Курсов',
        record: 'Форма с модального окна Запись на Курс',
        question: 'Форма с модального окна Задать Вопрос',
        job: 'Форма с модального окна Вакансия'
      };

      return (formNameList[brief]) ? formNameList[brief] : false;
    },
    checkRequired(data) {
        let validData = {
            phone: (val) => {
                // val = val.replace(/[()-\s+]/g, '');

                let valid = (val.length > 5) ? true : false;

                return (valid) ? true : false;
            },
            email: (val) => {
                let re = /^([A-Za-z0-9_\-.])+@([A-Za-z0-9_\-.])+\.([A-Za-z]{2,4})$/;
                let valid = re.test(val);

                return (valid) ? true : false;
            }
        };

        for(let input in data) {
          let value = data[input].value;

          if (data[input].required && !value) {
            data[input].not_valid = true;
            let message = (input == 'check') 
              ? 'Вы не дали согласие на обработку персональных данных'
              : 'Заполнены не все обязательные данные'

            this.alertMessage(message, true);

            return false;
          }

          if (data[input].valid && !validData[data[input].valid](value)) {
            this.alertMessage('Даные введены неверно.', true);

            return false;
          }
        }

        return true;
    },
    alertMessage(message, danger){
        this.alertShow = true;
        this.alertDanger = danger;
        this.message = message;
        
        setTimeout(() => {
          if (this.alertShow) this.alertShow = false;
        }, 3000);
    },

    clickPlayVideo(e, id, container) {
      let iframe = document.createElement("iframe");
      let iframe_url = "https://www.youtube.com/embed/" + id + "?autoplay=1&autohide=1";
      let target = e.target;

      target.classList.add('hidden');
      target.parentElement.classList.add('play');

      iframe.setAttribute("src", iframe_url);
      iframe.setAttribute("frameborder",'0');
      iframe.style.width  = '100%';
      iframe.style.height = '100%';
      
      (Array.isArray(this.$refs[container]))
        ? this.$refs[container][0].appendChild(iframe)
        : this.$refs[container].appendChild(iframe);
    },
  }
}

