<template>
    <section class="modal" @click="closeModal">
        <div>
          <div class="modal__bcg"></div>
          <div class="modal__content" @click.stop="">
            <div class="modal__header">
                <h2 class="modal__caption">Запись на обучение</h2>
                <button class="btn-close" @click="closeModal">
                    <span></span>
                    <span></span>
                </button>
            </div>
            <div class="modal__body">
                <p class="p-text__16">Отправь заявку и наш специалист свяжется с тобой для записи:</p>
                <form action="mail.php" class="form" :class="{load: send}">
                    <label class="label">
                        <input name="name" type="text" placeholder="Имя" 
                          v-model="dataList.name.value"
                        >
                    </label>
                    <label class="label">
                        <input name="phone" 
                          type="text" 
                          class="input__phone"
                          :class="{'not-valid': dataList.phone.not_valid}"
                          placeholder="Телефон*"
                          v-mask="'+7(###)###-##-##?#'"
                          v-model="dataList.phone.value"
                          @change="validInput('phone')"
                        >
                    </label>

                    <label class="label-check">
                      <input type="checkbox"
                        v-model="dataList.check.value"
                      >
                        <span></span><p>Я даю согласие на обработку моих персональных данных</p>
                    </label>

                    <input type="hidden" name="type" value="record" v-model="dataList.type.value">
                    <input type="hidden" name="test" value="" v-model="dataList.test.value">

                    <button type="submit" class="btn-1" 
                      @click.prevent="submit('record')"
                      :disabled="send"
                    >Отправить</button>

                    <div class="alert-close" v-if="alertShow" @click="alertShow = false">
                      <div 
                        class="alert"
                        :class="[(alertDanger) ? 'alert-danger' : 'alert-success']"
                      >
                      {{ message }}
                      </div>
                    </div>
                </form>
            </div>
          </div>
        </div>
      </section>
</template>

<script>
import Vue from 'vue';
import VueMask from 'v-mask'
import SendMxin from '../send-mixin';

Vue.use(VueMask);

export default {
  name: 'Record',
  mixins: [SendMxin],
  data() {
    return{
      dataList: {
        name: {
          value: ''
        },
        phone: {
          value: '',
          required: true,
          valid: 'phone',
          not_valid: false
        },
        check: {
          value: false,
          required: true,
        },
        test: {
          value: ''
        },
        type: {
          value: 'record'
        },
      },

      send: false,

      alertDanger: false,
      alertShow: false,
      message: ''
    };
  },
  created() {
    document.body.style.cssText = 'overflow: hidden;';
  },
  methods: {
    validInput(input) {
      if (this.dataList[input].not_valid) this.dataList[input].not_valid = false;
    },
    closeModal() {
      document.body.style.cssText = '';
      this.$emit('emitclosemodal');
    },
  }
}
</script>

